var that;
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			navtitle: [
				{
					id: 0,
					name: '平台商品'
				},
				{
					id: 1,
					name: '商家商品'
				},
				{
					id: 2,
					name: '我的商品'
				}
			],
			goods_list: [],
			shop_list: [],
			mine_list: [],
			mpage: 1,
			mnomore: false,
			mnolist: false,
			spage: 1,
			snomore: false,
			snolist: false,
			num: 0,
			page: 1,
			nomore: false,
			nolist: false,
			idlist: [],
			ids: [],
			maxnum: 10
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			// if (e.currentTarget.dataset.id == 0) {
			// 	that.page = 1;
			// 	that.goods_list = [];
			// 	that.nolist = false;
			// 	that.nomore = false;
			// 	that.getlist1();
			// } else if (e.currentTarget.dataset.id == 1) {
			// 	that.spage = 1;
			// 	that.shop_list = [];
			// 	that.snolist = false;
			// 	that.snomore = false;
			// 	that.getlist2();
			// } else if (e.currentTarget.dataset.id == 2) {
			// 	that.mpage = 1;
			// 	that.mine_list = [];
			// 	that.mnolist = false;
			// 	that.mnomore = false;
			// 	that.getlist3();
			// }
		},
		printl(e) {
			var TabCur = that.TabCur;
			if (TabCur == 0) {
				let index = e.currentTarget.dataset.index;
				let goods_list = that.goods_list;
				if (!goods_list[index]['print']) {
					if (that.idlist.length + that.ids.length == that.maxnum) {
						return;
					}
					goods_list[index]['print'] = !goods_list[index]['print'];
					that.idlist.push(goods_list[index].id);
					console.log(that.idlist);
					that.num++;
				} else {
					goods_list[index]['print'] = !goods_list[index]['print'];
					var idindex = that.idlist.indexOf(goods_list[index].id);
					that.idlist.splice(idindex, 1);
					that.idlist = that.idlist;
					console.log(that.idlist);
					that.num--;
				}
				that.goods_list = goods_list;
				that.$forceUpdate();
			} else if (TabCur == 1) {
				let index = e.currentTarget.dataset.index;
				let shop_list = that.shop_list;
				if (!shop_list[index]['print']) {
					shop_list[index]['print'] = !shop_list[index]['print'];
					if (that.idlist.length + that.ids.length == that.maxnum) {
						return;
					}
					that.idlist.push(shop_list[index].id);
					console.log(that.idlist);
					that.num++;
				} else {
					shop_list[index]['print'] = !shop_list[index]['print'];
					var idindex = that.idlist.indexOf(shop_list[index].id);
					that.idlist.splice(idindex, 1);
					that.idlist = that.idlist;
					console.log(that.idlist);
					that.num--;
				}
				that.shop_list = shop_list;
				that.$forceUpdate();
			} else if (TabCur == 2) {
				let index = e.currentTarget.dataset.index;
				let mine_list = that.mine_list;
				if (!mine_list[index]['print']) {
					if (that.idlist.length + that.ids.length == that.maxnum) {
						return;
					}
					mine_list[index]['print'] = !mine_list[index]['print'];
					that.ids.push(mine_list[index].id);
					console.log(that.ids);
					that.num++;
				} else {
					mine_list[index]['print'] = !mine_list[index]['print'];
					var idindex = that.ids.indexOf(mine_list[index].id);
					that.ids.splice(idindex, 1);
					that.ids = that.ids;
					console.log(that.ids);
					that.num--;
				}
				that.mine_list = mine_list;
				that.$forceUpdate();
			}
		},
		// 平台商品
		getlist1() {
			var that = this;
			that.$request
				.request({
					url: that.api.anchor_order.goods_list,
					method: 'GET',
					data: {
						page: that.page,
						type: 1
					}
				})
				.then(res => {
					console.log(res);
					if (res.length > 0) {
						that.goods_list = that.goods_list.concat(res);
						that.page = that.page + 1;
					} else {
						if (that.page != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了!');
								that.nomore = true;
							}
						} else {
							that.nolist = true;
						}
					}
				});
		},
		getlist2() {
			var that = this;
			that.$request
				.request({
					url: that.api.anchor_order.goods_list,
					method: 'GET',
					data: {
						page: that.spage,
						type: 2
					}
				})
				.then(res => {
					console.log(res);
					if (res.length > 0) {
						that.shop_list = that.shop_list.concat(res);
						that.spage = that.spage + 1;
					} else {
						if (that.spage != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了!');
								that.snomore = true;
							}
						} else {
							that.snolist = true;
						}
					}
				});
		},
		getlist3() {
			var that = this;
			that.$request
				.request({
					url: that.api.anchor_order.goods_list,
					method: 'GET',
					data: {
						page: that.mpage,
						type: 3
					}
				})
				.then(res => {
					console.log(res);
					if (res.length > 0) {
						that.mine_list = that.mine_list.concat(res);
						that.mpage = that.mpage + 1;
					} else {
						if (that.mpage != 1) {
							if (!that.nomore) {
								that.$alert('没有更多了!');
								that.mnomore = true;
							}
						} else {
							that.mnolist = true;
						}
					}
				});
		},
		choosedone() {
			if (that.idlist.length == 0 && that.ids.length == 0) {
				uni.showToast({
					title: '请选择至少一件商品',
					icon: 'none'
				});
				return false;
			}
			var ids = that.ids;
			var idlist = that.idlist;
			uni.setStorageSync('goods', idlist);
			uni.setStorageSync('anchor_goods', ids);
			uni.navigateBack({
				delta: 1
			});
		}
	},
	onLoad() {
		that = this;
		that.getlist1();
		that.getlist2();
		that.getlist3(); 
		that.maxnum = uni.getStorageSync('config').live_goods_num;
	},
	onReachBottom() {
		that = this;
		var TabCur = that.TabCur;
		if (TabCur == 0) {
			that.getlist1();
		} else if (TabCur == 1) {
			that.getlist2();
		} else if (TabCur == 2) {
			that.getlist3();
		}
	}
};
